.category_lists_block {
    margin-left: 25px;
    display: flex;
    flex-direction: column;
}

.category_lists_block h2 {
    padding: 0;
    margin-bottom: 0;

    font-size: 25px;
    color: var(--light-green-main);
}

.category_lists_block p {
    font-style: italic;
    margin-top: 0;
}


.category_list {
    margin: 0;
    /* margin-left: 25px; */
    margin-bottom: 25px;
    padding: 0;

    font-size: 30px;
    list-style: none;
    flex-basis: 50%;
}

.products_list {
    font-size: 20px;
    list-style: none;
}

.products_item {
    margin-bottom: 5px;
}

.category_name {
    margin: 0;
    margin-bottom: 10px;

    color: var(--light-green-main);
    font-size: 25px;
}

.deleted_products_block h2 {
    color: var(--error);
}

@media only screen and (max-width: 768px) {
    .products_list {
        font-size: 17px;
    }

    .category_lists_block {
        width: 50%;
    }
}